import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import { GiHamburgerMenu } from "react-icons/gi";
import { IoMdArrowDropdown } from "react-icons/io";
import { Avatar } from "@mui/material";
import { getProfile } from "../api/Profile";
import { getCourses } from "../api/Courses";
import { logoutUser } from "../api/User";
import { MdDarkMode } from "react-icons/md";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";
import useSWR from "swr";

const Header = ({ page }) => {
  const [darkMode, setDarkMode] = useState(
    JSON.parse(localStorage.getItem("md")) !== null
      ? JSON.parse(localStorage.getItem("md"))
      : true
  );
  const [position, setPosition] = useState(window.pageYOffset);
  const [styled, setStyled] = useState(false);
  const [visible, setVisible] = useState(true);
  useEffect(() => {
    const handleScroll = () => {
      let moving = window.pageYOffset;

      setVisible(position > moving ? true : position < 150 || position > 500);
      setStyled(position > 500);
      setPosition(moving);
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  });

  const [menuOpen, setMenuOpen] = useState(false); //for menu dropdown
  const [programmesMenuOpen, setProgrammesMenuOpen] = useState(false); //for menu dropdown
  const [mainOpen, setMainOpen] = useState(false); //for the header dropdown
  const menuDropdown = useRef(null);
  const programmesMenuDropdown = useRef(null);
  const menu = useRef(null);
  const programmesMenu = useRef(null);
  const dropdown = useRef(null);
  const mainDropdown = useRef(null);
  const mainDropdownRef = useRef(null);
  const [avatar, setAvatar] = useState("");

  window.addEventListener("click", (e) => {
    if (
      mainDropdownRef &&
      mainDropdown &&
      !mainDropdownRef.current?.contains(e.target) &&
      mainOpen &&
      !mainDropdown.current?.contains(e.target)
    ) {
      setMainOpen(false);
    }

    if (
      dropdown &&
      menu &&
      !menu.current?.contains(e.target) &&
      !dropdown.current?.contains(e.target) &&
      menuOpen
    ) {
      menuDropdown.current?.classList.remove("rotate-180");
      setMenuOpen(false);
    }

    if (
      programmesMenu &&
      !programmesMenuDropdown.current?.contains(e.target) &&
      programmesMenuOpen
    ) {
      programmesMenuDropdown.current?.classList.remove("rotate-180");
      setProgrammesMenuOpen(false);
    }
  });

  const { data: profileData, error: profileError } = useSWR(
    process.env.REACT_APP_BACKEND + "/user/profile",
    async (url) => await getProfile(url), { revalidateOnFocus: false, revalidateOnReconnect: true }
  );
  const { data: coursesData, error: coursesError } = useSWR(
    process.env.REACT_APP_BACKEND + `/courses/list`,
    async (url) => await getCourses(url), { revalidateOnFocus: false, revalidateOnReconnect: true }
  );
  const [logged, setLogged] = useState(profileData?.success); //for hiding or showing logged user profile

  const menuOpenToggle = () => {
    if (!menuOpen) {
      menuDropdown.current.classList.add("rotate-180");
      setMenuOpen(true);
    } else {
      menuDropdown.current.classList.remove("rotate-180");
      setMenuOpen(false);
    }
  };

  const programmesMenuOpenToggle = () => {
    if (!programmesMenuOpen) {
      programmesMenuDropdown.current.classList.add("rotate-180");
      setProgrammesMenuOpen(true);
    } else {
      programmesMenuDropdown.current.classList.remove("rotate-180");
      setProgrammesMenuOpen(false);
    }
  };

  const mainOpenToggle = () => {
    if (!mainOpen) {
      setMainOpen(true);
    } else {
      setMainOpen(false);
    }
  };

  const logout = async () => {
    await logoutUser(process.env.REACT_APP_BACKEND + "/user/logout/");
    window.location.reload();
  };

  const changeDarkMode = (e) => {
    e.preventDefault();
    const root = window.document.documentElement;
    if (darkMode) {
      localStorage.setItem("md", false);
      root.classList.remove("dark");
      setDarkMode(false);
    } else {
      localStorage.setItem("md", true);
      root.classList.add("dark");
      setDarkMode(true);
    }
  };

  useEffect(() => {
    const root = window.document.documentElement;
    if (darkMode) {
      root.classList.add("dark");
    } else {
      root.classList.remove("dark");
    }

    if (typeof profileData !== typeof undefined) {
      if (profileData?.success) {
        setLogged(true);
        setAvatar(profileData?.data?.profilePicture);
      } else {
        setLogged(false);
      }
    } else if (profileError) {
      setLogged(false);
    }
  }, [profileData, darkMode, profileError]);

  return (
    <div
      className={`${visible ? "visible animate-moveDown" : "hidden"} ${styled ? "bg-back dark:bg-secondBackground shadow-md" : " bg-transparent"
        } dark:text-mainText/80 fixed z-30 top-0 w-full h-[60px] md:h-[80px] transition ease-out duration-1000 flex justify-between items-center  select-none `}
    >
      <div className="flex items-center gap-5 lg:gap-10">
        <Link to="/">
          <img
            className="dark:hidden max-w-[140px] md:max-w-[160px] max-h-fit object-contain ml-5 z-50"
            alt=""
            src="/logo.png"
          />
          <img
            className="dark:flex hidden max-w-[140px] md:max-w-[160px] max-h-fit object-contain ml-5"
            alt=""
            src="/logo2.png"
          />
        </Link>
        <Link
          to="/"
          className={`hover:text-blue-600 hover:translate-x-0.5 hover:translate-y-0.5 transition-all hidden md:flex hover:cursor-pointer font-sans text-lg items-center`}
        >
          Home
        </Link>
        <div
          ref={programmesMenu}
          className={`mr-4 hidden md:flex w-[100px] hover:text-blue-600 hover:cursor-pointer font-sans text-lg items-center relative`}
        >
          <Link
            to="/programmes"
            className={`hover:text-blue-600 hover:translate-x-0.5 hover:translate-y-0.5 hidden md:flex hover:cursor-pointer font-sans text-lg items-center`}
          >
            Programmes
          </Link>
          <span onClick={programmesMenuOpenToggle} ref={programmesMenuDropdown}>
            <IoMdArrowDropdown size={25} />
          </span>

          <div
            className={` absolute ${!programmesMenuOpen ? "hidden" : null
              } dark:bg-slate-800/20 dark:text-white backdrop-blur-sm transition dark:duration-1000 text-gray-700 top-[55px] shadow-md bg-back/20 w-fit -right-6`}
          >
            {!coursesData && !coursesError ? (
              <>
                <div className="py-1 px-4 w-28 block">
                  <Skeleton baseColor="gray" />
                </div>
                <div className="py-1 px-4 w-28 block">
                  <Skeleton baseColor="gray" />
                </div>
              </>
            ) : (
              coursesData?.data?.map((course, i) => (
                <Link
                  key={i}
                  to={`/programmes/${course?._id}`}
                  className="rounded-b hover:bg-blue-400 hover:text-white py-2 px-4 block whitespace-nowrap"
                >
                  {course?.title}
                </Link>
              ))
            )}
          </div>
        </div>

        <Link
          to="/about-us"
          className={`hover:text-blue-600 hover:translate-x-0.5 hover:translate-y-0.5 hidden md:flex hover:cursor-pointer font-sans text-lg items-center`}
        >
          About
        </Link>
        <div
          onClick={changeDarkMode}
          className="hover:cursor-pointer dark:bg-slate-700 flex dark:justify-end transition-all border-2 w-10 rounded-xl px-0.5 items-center gap-1"
        >
          <MdDarkMode color="" />
        </div>
      </div>
      <span ref={mainDropdown} className="mr-5">
        <GiHamburgerMenu
          onClick={mainOpenToggle}
          className="z-50 md:hidden hover:cursor-pointer"
          size={25}
        />
      </span>
      {!profileData && !profileError ? (
        <div className="mr-[2vw] md:flex items-center hidden">
          <div>
            <Skeleton baseColor="gray" height={45} width={45} circle />
          </div>
        </div>
      ) : typeof logged !== typeof undefined ? (
        <div
          className={`w-1/2 h-full justify-end gap-[2vw] mr-[2vw] items-center text-lg md:flex hidden`}
        >
          <div className={`${!logged ? "md:flex gap-[2vw]" : "hidden"}`}>
            <Link
              to="/signup"
              className={`transition duration-200 font-medium bg-footer hover:bg-white hover:text-black text-white py-2 px-4 rounded-xl text-xl`}
            >
              Sign Up
            </Link>
            <Link
              to="/login"
              className="transition duration-200 font-medium font-sans rounded-lg px-4 hover:translate-x-[0.5px] hover:translate-y-[0.5px] py-2 text-xl"
            >
              Login
            </Link>
          </div>
          <div
            ref={menu}
            className={`${logged ? "md:flex" : "hidden"
              } w-[100px] hover:text-blue-600 hover:cursor-pointer font-sans text-lg items-center relative`}
            onClick={menuOpenToggle}
          >
            <span>
              <Avatar alt="" src={avatar} />
            </span>
            <span ref={menuDropdown}>
              <IoMdArrowDropdown size={25} />
            </span>

            <div
              ref={dropdown}
              className={` absolute ${!menuOpen ? "hidden" : null
                } dark:bg-slate-800/40 dark:text-white backdrop-blur-sm transition dark:duration-1000 text-gray-700 pt-1 top-[60px] shadow-md bg-blue-50 w-full right-8`}
            >
              <Link
                to="/profile"
                className="rounded-b hover:bg-blue-400 hover:text-white py-2 px-4 block whitespace-nowrap"
              >
                Profile
              </Link>
              {/* <Link
                  to="/payment"
                  className="rounded-t hover:bg-blue-400 hover:text-white py-2 px-4 block whitespace-nowrap">
                  Payment
                </Link> */}
              <Link
                to="/"
                onClick={logout}
                className="hover:bg-blue-400 hover:text-white py-2 px-4 block whitespace-nowrap"
              >
                Log Out
              </Link>
            </div>
          </div>
        </div>
      ) : null}

      <div
        ref={mainDropdownRef}
        className={`absolute ${!mainOpen ? "hidden" : "animate-moveHeaderDown"
          } md:animate-none flex flex-col dark:bg-background/90 dark:text-white backdrop-blur-lg md:hidden dark:border-slate-800/40 border-t-2 text-gray-700 text-center text-lg font-medium rounded-lg bg-white/90 top-[60px] shadow-lg z-50 w-full`}
      >
        <Link
          to="/"
          className="rounded-t hover:bg-blue-500 hover:text-white py-2 px-4"
        >
          Home
        </Link>
        <Link
          to="/profile"
          className={`${!logged ? "hidden" : "null"
            } hover:text-white hover:bg-blue-600 py-2 px-4`}
        >
          Profile
        </Link>
        {/* <Link
          to="/payment"
          className={`${
            !logged ? "hidden" : "null"
          } hover:text-white hover:bg-blue-600 py-2 px-4`}
        >
          Payment
        </Link> */}
        <Link
          to="/programmes"
          className="rounded-t hover:bg-blue-600 hover:text-white py-2 px-4"
        >
          Programmes
        </Link>
        <Link
          to="/about-us"
          className="rounded-t hover:bg-blue-600 hover:text-white py-2 px-4"
        >
          About
        </Link>
        <Link
          to="/signup"
          className={`${logged ? "hidden" : "null"
            } rounded-b hover:text-white hover:bg-blue-600 py-2 px-4`}
        >
          Sign Up
        </Link>
        <Link
          to="/login"
          className={`${logged ? "hidden" : "null"
            } hover:text-white hover:bg-blue-600 py-2 px-4`}
        >
          Login
        </Link>
        <Link
          to="/"
          onClick={logout}
          className={`${!logged ? "hidden" : "null"
            } hover:text-white hover:bg-blue-600 py-2 px-4`}
        >
          Log Out
        </Link>
      </div>
    </div>
  );
};

export default Header;
